<template>
  <div class="app-container">
    <el-form :inline="true" :model="searchForm" size="mini">
      <el-row>
        <el-col :span="4">
          <el-form-item label="所属体系">
            <el-select
              v-model="searchForm.system"
              clearable
              placeholder="请选择"
            >
              <el-option
                v-for="item in systemOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item label="课程类型">
            <el-select
              v-model="searchForm.courseType"
              clearable
              size="mini"
              placeholder="请选择"
            >
              <el-option
                v-for="item in courseTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item label="所属产品线">
            <el-select
              v-model="searchForm.product"
              clearable
              size="mini"
              placeholder="请选择"
            >
              <el-option
                v-for="item in productOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="培训名称">
            <el-input
              v-model="searchForm.trainName"
              size="mini"
              placeholder="请输入"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item label="所属系列">
            <el-input
              v-model="searchForm.series"
              size="mini"
              placeholder="请输入"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="3">
          <el-form-item>
            <el-button type="primary" size="mini" @click="query"
              >查询</el-button
            >
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <el-row type="flex" justify="end">
      <el-button type="primary" size="mini" @click="addTraining"
        >新增</el-button
      >
    </el-row>
    <el-table
      v-loading="listLoading"
      :data="list"
      border
      fit
      size="mini"
      stripe
      highlight-current-row
      style="width: 100%;margin-top:15px"
    >
      <el-table-column
        header-align="center"
        align="center"
        :label="$t('table.id')"
        width="80"
        sortable="true"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.id }}</span>
        </template>
      </el-table-column>

      <el-table-column
        min-width="150px"
        header-align="center"
        label="培训名称"
        sortable
      >
        <template slot-scope="scope">
          <span>{{ scope.row.trainingName }}</span>
        </template>
      </el-table-column>

      <el-table-column
        min-width="150px"
        header-align="center"
        label="所属系列"
        sortable
      >
        <template slot-scope="scope">
          <span>{{ scope.row.series }}</span>
        </template>
      </el-table-column>

      <el-table-column
        min-width="150px"
        header-align="center"
        label="所属体系"
        sortable
      >
        <template slot-scope="scope">
          <span>{{ scope.row.sort }}</span>
        </template>
      </el-table-column>

      <el-table-column
        label="课程类型"
        header-align="center"
        min-width="110"
        sortable
      >
        <template slot-scope="scope">
          <span>{{ scope.row.type }}</span>
        </template>
      </el-table-column>

      <el-table-column width="100px" label="所属产品线" header-align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.bu }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="创建人"
        header-align="center"
        width="110"
        sortable
      >
        <template slot-scope="scope">
          <span>{{ scope.row.creatorName }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="创建时间"
        header-align="center"
        min-width="140"
        sortable
      >
        <template slot-scope="scope">
          <span>{{ scope.row.createTime | formatDate }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="最后更新人"
        header-align="center"
        min-width="110"
        sortable
      >
        <template slot-scope="scope">
          <span>{{ scope.row.updateName }}</span>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        fixed="right"
        :label="$t('table.option')"
        width="280"
        header-align="center"
      >
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="mini"
            icon="el-icon-edit"
            @click="handleEdit(scope.row);"
            >编辑</el-button
          >
          <el-button
            type="primary"
            size="mini"
            icon="el-icon-delete"
            @click="handleDel(scope.row.trainingId);"
            >删除</el-button
          >
          <el-button
            type="primary"
            size="mini"
            icon="el-icon-search"
            @click="handleDetail(scope.row);"
            >查看</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="45%">
      <el-form
        :model="trainForm"
        size="mini"
        label-width="120px"
        ref="trainForm"
        :rules="rules"
      >
        <el-row>
          <el-col v-if="!isAdd || isDetail">
            <el-form-item label="培训编号" prop="trainId">
              <el-input
                v-model="trainForm.trainId"
                autocomplete="off"
                disabled
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col>
            <el-form-item label="培训名称" prop="trainName">
              <el-input
                v-model="trainForm.trainName"
                autocomplete="off"
                required
                maxlength="64"
                :disabled="isDetail"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="所属系列" prop="series">
              <el-input
                v-if="isDetail"
                v-model="trainForm.series"
                autocomplete="off"
                required
                maxlength="64"
                disabled
              ></el-input>
              <el-select
                v-else
                v-model="trainForm.series"
                filterable
                allow-create
                default-first-option
                placeholder="请选择"
              >
                <el-option
                  v-for="item in seriesOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="所属体系" prop="system">
              <el-input
                v-if="isDetail"
                v-model="trainForm.system"
                autocomplete="off"
                required
                maxlength="64"
                disabled
              ></el-input>
              <el-select
                v-else
                v-model="trainForm.system"
                filterable
                allow-create
                default-first-option
                placeholder="请选择"
              >
                <el-option
                  v-for="item in systemOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="课程类型" prop="courseType">
              <el-input
                v-if="isDetail"
                v-model="trainForm.courseType"
                autocomplete="off"
                required
                maxlength="64"
                disabled
              ></el-input>
              <el-select
                v-model="trainForm.courseType"
                placeholder="请选择"
                v-else
              >
                <el-option
                  v-for="item in courseTypeOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="培训类型" prop="trainType">
              <el-input
                v-if="isDetail"
                v-model="trainForm.trainType"
                autocomplete="off"
                required
                maxlength="64"
                disabled
              ></el-input>
              <el-select
                v-model="trainForm.trainType"
                placeholder="请选择"
                v-else
              >
                <el-option
                  v-for="item in trainTypeOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="培训时长(小时)" prop="trainTime">
              <el-input
                v-model="trainForm.trainTime"
                autocomplete="off"
                :disabled="isDetail"
                maxlength="4"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="所属产品线" prop="product">
              <el-input
                v-if="isDetail"
                v-model="trainForm.product"
                autocomplete="off"
                required
                maxlength="64"
                disabled
              ></el-input>
              <el-select
                v-model="trainForm.product"
                placeholder="请选择"
                v-else
              >
                <el-option
                  v-for="item in productOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="培训简介" prop="description">
          <el-input
            :disabled="isDetail"
            type="textarea"
            v-model="trainForm.description"
            :autosize="{ minRows: 2, maxRows: 4 }"
            resize="none"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false;" size="mini" v-if="!isDetail"
          >取 消</el-button
        >
        <el-button @click="dialogVisible = false;" size="mini" v-if="isDetail"
          >关闭</el-button
        >
        <el-button
          type="primary"
          @click="submtForm('trainForm');"
          size="mini"
          v-if="!isDetail"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Cookies from "js-cookie";
import { formatDate } from "@/utils/date";
export default {
  filters: {
    formatDate(time) {
      let date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm:ss");
    }
  },
  data() {
    let validaNumer = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("请输入培训时长"));
      }
      if (isNaN(value)) {
        return callback(new Error("只能输入整数和小数"));
      }
      return callback();
    };
    return {
      systemOptions: [],
      courseTypeOptions: [
        { label: "公开课", value: "公开课" },
        { label: "专业课", value: "专业课" }
      ],
      productOptions: [{ label: "2012", value: "2012" }],
      trainTypeOptions: [
        { label: "选修", value: "选修" },
        { label: "必修", value: "必修" }
      ],
      seriesOptions: [],
      searchForm: {
        system: "",
        courseType: "",
        product: "",
        trainName: "",
        series: ""
      },
      trainForm: {
        trainId: "",
        trainName: "",
        description: "",
        series: "",
        system: "",
        courseType: "",
        trainType: "",
        trainTime: "",
        product: ""
      },
      list: null,
      listLoading: false,
      page: {
        pageNum: 1,
        pageSize: 100
      },
      dialogTitle: "添加培训信息",
      dialogVisible: false,
      isAdd: true,
      isDetail: false,
      rules: {
        trainName: [
          { required: true, message: "请输入培训名称", trigger: "blur" }
        ],
        description: [
          { required: true, message: "请输入培训简介", trigger: "blur" }
        ],
        series: [
          { required: true, message: "请选择/输入所属系列", trigger: "change" }
        ],
        system: [
          { required: true, message: "请选择/输入所属体系", trigger: "change" }
        ],
        courseType: [
          { required: true, message: "请选择课程类型", trigger: "change" }
        ],
        trainType: [
          { required: true, message: "请选择培训类型", trigger: "change" }
        ],
        trainTime: [
          { trigger: "blur", validator: validaNumer, required: true }
        ],
        product: [
          { required: true, message: "请选择产品线", trigger: "change" }
        ]
      }
    };
  },
  mounted() {
    this.getProductInfo();
    this.getSeries();
    this.getSystem();
    this.getTrainingList();
  },
  methods: {
    query() {
      this.getTrainingList();
    },
    getTrainingList() {
      let vm = this;
      let condition = {
        bu: vm.searchForm.product,
        series: vm.searchForm.series,
        sort: vm.searchForm.system,
        trainingName: vm.searchForm.trainName,
        classType: vm.searchForm.courseType
      };
      vm.listLoading = true;
      vm.$store
        .dispatch("getTrainingList", condition)
        .then(data => {
          if (data) {
            vm.list = data.map((item, index) => {
              item.id = index + 1;
              return item;
            });
          } else {
            vm.list = [];
          }
          vm.listLoading = false;
        })
        .catch(() => {
          vm.list = [];
          vm.listLoading = false;
        });
    },
    addTraining() {
      this.trainForm.trainId = "";
      this.trainForm.trainName = "";
      this.trainForm.description = "";
      this.trainForm.series = "";
      this.trainForm.system = "";
      this.trainForm.courseType = "";
      this.trainForm.trainType = "";
      this.trainForm.trainTime = "";
      this.trainForm.product = "";
      this.dialogTitle = "添加培训信息";
      this.dialogVisible = true;
      this.isAdd = true;
      this.clearValidate();
      this.getProductInfo();
      this.getSeries();
      this.getSystem();
    },
    submtForm() {
      let vm = this;
      vm.$refs.trainForm.validate(valid => {
        if (valid) {
          let formData = {
            bu: vm.trainForm.product,
            series: vm.trainForm.series,
            sort: vm.trainForm.system,
            trainingName: vm.trainForm.trainName,
            trainingDesc: vm.trainForm.description,
            trainingDuration: vm.trainForm.trainTime,
            type: vm.trainForm.trainType
          };
          if (vm.isAdd) {
            formData.creatorId = Cookies.get("loginName");
          } else {
            formData.trainingId = vm.trainForm.trainId;
            formData.updaterId = Cookies.get("loginName");
          }
          vm.$store.dispatch("saveTrainingInfo", formData).then(res => {
            if (res) {
              vm.$message.success("提交成功");
              vm.dialogVisible = false;
              vm.getTrainingList();
            }
          });
        } else {
          return false;
        }
      });
    },
    clearValidate() {
      if (this.$refs.trainForm) {
        this.$refs.trainForm.clearValidate();
      }
    },
    getProductInfo() {
      let vm = this;
      vm.$store.dispatch("getProductInfo").then(() => {
        const data = vm.$store.getters.productList;
        if (data) {
          vm.productOptions = data;
        } else {
          vm.productOptions = [];
        }
      });
    },
    getSeries() {
      let vm = this;
      vm.$store.dispatch("querySeries").then(res => {
        if (res.data) {
          vm.seriesOptions = res.data.map(item => {
            return {
              label: item,
              value: item
            };
          });
        } else {
          vm.seriesOptions = [];
        }
      });
    },
    getSystem() {
      let vm = this;
      vm.$store.dispatch("querySystem").then(res => {
        if (res.data) {
          vm.systemOptions = res.data.map(item => {
            return {
              label: item,
              value: item
            };
          });
        } else {
          vm.systemOptions = [];
        }
      });
    },
    handleDel(id) {
      let vm = this;
      vm.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        vm.$store.dispatch("delTraining", id).then(res => {
          if (res === "success") {
            vm.$message.success("操作成功");
            vm.getTrainingList();
          } else {
            vm.$message.error("操作失败");
          }
        });
      });
    },
    handleEdit(rowData) {
      this.getProductInfo();
      this.getSeries();
      this.getSystem();
      this.trainForm.trainId = rowData.trainingId;
      this.trainForm.trainName = rowData.trainingName;
      this.trainForm.description = rowData.trainingDesc;
      this.trainForm.series = rowData.series;
      this.trainForm.system = rowData.sort;
      // this.trainForm.courseType = rowData.type;
      this.trainForm.trainType = rowData.type;
      this.trainForm.trainTime = rowData.trainingDuration;
      this.trainForm.product = rowData.bu;
      this.dialogTitle = "修改培训信息";
      this.dialogVisible = true;
      this.clearValidate();
    },
    handleDetail(rowData) {
      this.trainForm.trainId = rowData.trainingId;
      this.trainForm.trainName = rowData.trainingName;
      this.trainForm.description = rowData.trainingDesc;
      this.trainForm.series = rowData.series;
      this.trainForm.system = rowData.sort;
      // this.trainForm.courseType = rowData.type;
      this.trainForm.trainType = rowData.type;
      this.trainForm.trainTime = rowData.trainingDuration;
      this.trainForm.product = rowData.bu;
      this.dialogTitle = "查看培训信息";
      this.dialogVisible = true;
      this.isDetail = true;
      this.clearValidate();
    }
  }
};
</script>
<style rel="stylesheet/scss" lang="scss"></style>
